body {
    margin: 0;
}

.LoginPage{
    display: grid;
    margin: 0;
    padding: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: left;
    background-position-y: bottom;
    background-attachment: fixed;
    min-height: 100vh;
    display: flex;
}

.Login_window {
    margin-top: 30px;
    width: 510.5px;
    min-height: 88%;
    max-height: 850px;
    background: #E5E5E5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 40px;
    
}
@media(max-width:850px){
    .LoginPage{
        width: 100vw;
    }
    .Login_window {
        width: 90vw;
        padding-left:5px;
        padding-right: 5px;
        border-radius: 0;
        margin-bottom: 5px;
    }
}
.Banner{
width: 462px;
height: 64px;
margin-left: auto;
margin-right: auto;
margin-top: 40.98px;
margin-bottom: 15px;
display: block;
background: rgba(196, 196, 196, 0.48);
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 10px;
font-family: Roboto;
font-style: normal;
font-weight: 200;
font-size: 48px;
line-height: 65px;
text-align: center;
}
.Window_input{
    width: 384px;
    display: block;
    height: 32px;
    top: 275px;
    background: rgba(154, 204, 11, 0.39);
    border: 0;
    border-bottom: solid black 1px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
}
#Email{
   margin-top: 20%; 
}
#Password{
    margin-top:40px; 
 }
.Input__Description{
margin-left: 65px;
font-family: Roboto;
font-style: normal;
font-weight: 200;
font-size: 14px;
line-height: 134%;
margin-bottom: 0;
}
@media(max-width:850px){
    .Banner{
        width: 95%;
        
    }
    .Window_input{
        width: 95%;

    }
    .Input__Description{
        margin-left: 20px;
    }
}
#active{
    align-items: center;
    font-size: 22px;
    margin-left: auto;
    margin-right: auto;
    width: 200px;

}
.Button {
    width: 75%;
    height: 42px;
    background: #85D45F;
    border-radius: 11px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    margin-left: 64px;
    margin-top: 20px;
}
@media(max-width:850px){
    .Button{
        margin-left: 13%;

    }
}
:hover.Button{
    cursor: pointer;
}
.Footer {
    display: flex;
    position: fixed;
    margin-top: 20px;
    width: 100vw;
    background: #147421;
    align-items: center;
    justify-content: center;
    height: 35px;
    bottom: 0;
    margin-top: 10px;
    z-index: 2;
    color: white;
}
#activdiv{
    display: grid;
    grid-template-rows: 100px 30px;
}
#reset{
    display: block;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    font-size: 20px;
    text-align: center;
    color: rgb(0, 0, 0);
}
#back{
    display: block;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    font-size: 20px;
    text-align: center;
    color: rgb(6, 94, 28);
}
@media(max-width:850px){
    .Footer{
        margin: 0;
        text-align: center;
    }
}