.navigation__menu_item{
    font-size: large;
    font-weight: bold;
    border-radius: 4px ;
    border: none;
    transition-property: color;
    transition-duration: 0.4s;
}

.navigation__menu_item:hover{
    cursor: pointer;
    color: azure;
}
#Register{
    background-color: rgb(207, 204, 31);
}
#Login{
    background-color: rgb(241, 150, 12);

}
#Logout{
    background-color: rgb(255, 58, 58);
    
}

.description{
    width: 100vw;
    height:100vh;
    font-size: 35px;
    margin-left: auto;
    margin-right: auto;
    background: #000000d0;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.233);
    color: white;
}
.para{
    margin-top: 70px;
    margin-left: 40px;
    width: 60%;
    height: auto;
}
@media(max-width:850px){
    #Login, #Register{
        width: 100px;
        font-size: 18px;
        font-weight: 850;
        margin-right: 10px;
    }
    #Logout{
        width: 160px;
        margin-top: 10px;
        border-radius: 0;
    }
    .description{
        width: 100%;
        font-size: 24px;
        height: 100vh;
    }
    .para{
        margin-left: 10px;
        margin-top: 90px;
        width: 90%;
        height: auto;

    }
}





body {
    margin: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: left;
    background-position-y: bottom;
    background-attachment: fixed;
    
}
:root{
    --color-background:rgb(197, 197, 197);
}
nav{
    min-height: 40px;
    width: 100%;
    background: #147421;
    position: fixed;
    display: flex;
    left: 0;
    top: 0;
    right: 0;
    z-index: 2;
    box-shadow: 0 1px 10px 3px;
}
.navigation__menu {
    list-style: none;
    display: flex;
    width: 90%;
    margin-left: 10px;
    justify-content: space-around;
}
.navigation__mobile{
    display: none;
}
.navigation__menu_item {
    border: none;
    background: none;
    display: flex;
    margin-left: 40px;
    color: white;
    font-size: 18px;
    height: 100%;
}
@media(max-width:850px) {
    .navigation__mobile {
        display: flex;
        background: none;
        border: none;
        padding: 0;
        z-index: 2;
        margin-left: 12px;
    }
}

:hover #navigation_button{
    cursor: pointer;  
}
#navigation_button{
    text-shadow: 1px 1px 2px black;
}
:hover.link{
    cursor: pointer;
    text-shadow: 1px 1px 2px rgb(33, 175, 168);
}
nav.Navigation a.active {
    color: red;
}
#navigation__menu_item_logout{
    width: 180px;
    background: #cf2a2a;
    border-radius: 11px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    justify-content: center;
    align-items: center;
    height: 29px;
    color:#000 ;
}
:hover#navigation__menu_item_logout{
    cursor: pointer;
    text-shadow: none;
    border-bottom: none;
}
.link{
    font-size: 20px;
    color: white;
    text-shadow: 1px 1px 2px black;
    text-decoration: none;
}
.MainFarm{
    margin-top: 88px;
    background-color: var(--color-background)


}
.MainFarm_section{  
    min-width: 220px;
    min-height: 250px;
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: rgb(225, 235, 235);
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 10px 2px;

    

}

.MainFarm_header{
    text-align: center;
    font-size: 28px;
    grid-column-start: 1;
    grid-column-end:3 ;
    display: block;
    height: 40px;
    margin-top: 0;

}
.f_table_head{
    font-size: 18px;
    background-color: rgb(149, 219, 161);
}
@media(max-width:750px){
    .MainFarm_section{
        width: 95vw;
        grid-template-columns: 100%;
    }
    .MainFarm_header{
        grid-column-start:1 ;
        grid-column-end: 2;

    }
    .f_table{
        border-top: 2px solid black;
        margin-top: 15px;

    }
}
#t_stat, #t_ostatnie{
    width: 95%;
    margin-left: auto;
    margin-right: auto;
}
#stat_head{
    background-color: rgb(149, 219, 161);
    font-size: 22px;
}

.f_table_element{
    font-size: 20px;
    background-color: rgb(218, 218, 218);
}

@media(max-width:850px){
    .Navigation{
        display: inline;

    }
    #Reg_link, #Log_link{
        border: none;
        line-height: normal;
        height: auto;
        width: auto;
    }
    #nav_main{
        list-style: none;
        position: fixed;
        display: none;
        width: 0;
        overflow: hidden;
        max-width: 290px;
        z-index: 9;
        padding: 0;
    }
    #showMenu {
        display: block;
        position: relative;
      }
    .navigation__mobile{
        height: 40px;
      }
    .navigation__menu_item, .link {
        display: block;
        text-decoration: none;
        font-size: 22px;
        height: 40px;
        line-height: 40px;
        width: 90vw;
        padding: 0;
        text-align: left;
        border-bottom: 1px solid black;
        align-self: center;
        margin-right: 10px;
    }
}
@media(max-width:850px){
    .navigation__menu{
        justify-content: space-around;
        width: 100%;
        padding: 0;
    }
    .navigation__menu_item{
        margin-left: 0;

    }

}